@use "../../scss" as *;
.profile {
  width: 100%;
  color: white;
  font-size: 1.8rem;
  margin-top: 20px;
  h1 {
    padding-bottom: 10px;
    border-bottom: 1px solid rgba($color: white, $alpha: 0.3);
  }
}
.information {
  @include flex(unset, unset);
  margin-top: 20px;
  p {
    max-width: 90%;
    overflow-wrap: break-word;
    color: rgba($color: $white, $alpha: 0.6);
    line-height: 1.5;
  }
}
.user__info {
  margin-bottom: 50px;
  width: 55%;
  h4 {
    margin-bottom: 10px;
    font-weight: 500;
  }
}
.delete__btn {
  text-align: center;
  button {
    border: 1px solid rgb(73 73 75);
    padding: 15px;
    border-radius: 25px;
    background-color: rgb(51 51 53);
    color: rgb(239 68 68);
    transition: all 0.25s ease;
    &:hover {
      background-color: rgb(239 68 68);
      color: rgb(255 255 255);
    }
  }
}
.email,
.name {
  margin: 30px 0;
  div {
    @include flex(center, space-between);
    button {
      svg:hover {
        color: rgb(14, 108, 230);
        cursor: pointer;
      }
    }
  }
}
.change__password {
  margin: 30px 0;
  div {
    form {
      @include flex(center, space-between);
      input {
        background-color: rgb(51 51 53);
        width: 350px;
        color: $white;
        padding: 10px;
        border-radius: 5px;
        outline: none;
        border-color: transparent;
      }
    }
    button {
      background-color: rgb(73 73 75);
      color: rgb(255 255 255);
      padding: 20px;
      border-radius: 10px;
      &:hover {
        background-color: rgb(14, 108, 230);
      }
    }
  }
  h4 {
    margin-bottom: 10px;
  }
}
.verified {
  @include flex(center, space-between);
  h4 {
    margin: 0;
  }
  button {
    padding: 0;
    font-size: 1.8rem;
    color: rgb(81 121 255);
    text-decoration: underline;
  }
}

.profile__photo {
  flex: 1;
  h4 {
    margin-bottom: 50px;
  }
  div {
    text-align: center;
    margin-bottom: 30px;
  }
  img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
  }
  input {
    display: none;
  }
  label {
    font-weight: 500;
    padding: 10px;
    width: 200px;
    margin: 0 auto;
    background-color: rgba($color: $white, $alpha: 0.1);
    border-radius: 30px;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
    span {
      margin-left: 20px;
    }

    &:hover {
      opacity: 0.7;
    }
  }
}
